    padding-left: 10px;
    font-size: 10px;

}
.Button__Button--QI7b2:hover{
    outline: none;
    box-shadow: inset 0px 0px 5px 0px  rgba(0,0,0, 0.7);
}

.Button__Button--QI7b2:active{
