.Task__Task--1jd-7{
    display: grid;
    justify-content: center;
}

.Task__Task--1jd-7 li{

    display: grid;
    grid-template-columns: 1fr;
    min-width: 100px;
    background: white;
    color: black;
    padding: 20px;
    border: 4px solid #eee;
    margin-bottom: 10px;
    border-radius: 7px;
    -webkit-transition: all 0.7s ease-in-out;
    transition: all 0.7s ease-in-out;
}
.Task__Task--1jd-7 h3{
    margin: 0;
    font-size: 30px;
    padding-right: 50px;
    display: inline-block;



}
.Task__Task--1jd-7 small{
    font-size: 10px;
    padding-top: 10px;
    text-align: right;
    float: right;
    display: inline-block;
}

.Task__Task--1jd-7 li:hover{
    border: 4px solid rebeccapurple;
    -webkit-transform: scale(1.05, 1.05);
            transform: scale(1.05, 1.05);

}