.MyForm__MyForm--3Ziik{
    display: flex;

    flex-direction: column;
    padding: 10px;
    flex-grow: 1;
    justify-content: start;

    align-items: center;

}
.MyForm__MyForm--3Ziik h1{
    padding-bottom: 10px;

}
.MyForm__MyForm--3Ziik input{
    height: 25px;
